body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toc-card-container {
  list-style: none;
  margin: 0.5em;
  padding: 0.65em;
  border-radius: 0.3em;
  color: #FFF;
  font-size: 0.85em;
  flex-grow: 1;
  flex-basis: 0;
}

.toc-card-text{
  list-style: none;
  text-align: left;
  line-height: 1.25em;
}

.toc-card-html p {
  margin: 0;
  margin-bottom: 0.5em;
}

.toc-stage-container .toc-card-html ul {
  list-style-type: circle;
}

.toc-stage-container .toc-card-html li {
  margin: 0 2em;
}
.toc-stage-container ul {
  list-style: none;
  margin: 0;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

.toc-stage-title {
  color: #04275A;
  height: 2em;
}

.toc-stage-section {
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  flex-wrap: wrap;
}
.toc-stages-container {
  padding: 1em 0;
  max-width: 1400px;
  margin: 0 auto; }

.toc-board-title-container {
  max-width: 1400px;
  margin: 0 auto; }

.toc-board-title-wrapper {
  background: #164EEE;
  color: #EDEDED;
  padding: 0.1em 2em;
  text-align: left;
  height: 5em; }
  .toc-board-title-wrapper img {
    height: 5em; }

@media (max-width: 699px) {
  .toc-board-title-wrapper {
    height: 10em; }
    .toc-board-title-wrapper .toc-board-title-container {
      text-align: center; }
    .toc-board-title-wrapper h1 {
      font-size: 1.25em; } }

@media (min-width: 701px) {
  .toc-board-title-wrapper h1 {
    display: inline-block; }
  .toc-board-title-wrapper img {
    float: right;
    display: inline-block; } }

.toc-tile {
  height: 5em;
  background: #2656d0;
  position: relative; }
  .toc-tile a {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    color: #DFD;
    text-decoration: none;
    -webkit-transition: 0.15s;
    transition: 0.15s; }
    .toc-tile a:hover {
      background: #1e44a5; }
    .toc-tile a .toc-tile-name {
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      left: 0;
      right: 0; }

body {
  background: #EDEDED;
}

.App {
  text-align: center;
  font-family: "Source Sans Pro", "Helvetica", "Arial", sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

